.dialogs{
    display: grid;
    grid-template-columns: 2fr 8fr;

}

.dialogsItems{
    padding: 10px;
    font-weight: bold;
    grid-column: 1;
}
.dialog a.activeDialog{
    color: darkblue;
}
.dialogsItems a{
    text-decoration: none;
    color: deepskyblue;
}

.dialog{
    padding: 10px;

}

.messages{
    padding: 10px;
    grid-column: 2;
}

.message{
    padding: 10px;

}
